import * as React from 'react';
import { Flex } from '@chakra-ui/core';
import Card from './Card';

const icons = ['ideas', 'structure', 'rent'];

export default function Description({ cards }) {
  return (
    <Flex wrap="wrap">
      {cards.map((card, index) => (
        <Card key={card.id} card={card} icon={icons[index]} index={index} />
      ))}
    </Flex>
  );
}
